import PriceChange from "@/components/PriceChange";
import Loading from "@/components/Loading";
import DataFormat from "@/components/DataFormat";
import {
	getAdminOrderList,
	setAdminOrderPrice,
	setAdminOrderRemark,
	setOfflinePay,
	setOrderRefund
} from "@/api/admin";
import {
	required,
	num
} from "@/utils/validate";
import {
	validatorDefaultCatch
} from "@/utils/dialog";
export default {
	name: "AdminOrderList",
	components: {
		PriceChange,
		Loading,
		DataFormat
	},
	props: {},
	data: function() {
		return {
			current: "",
			change: false,
			types: 0,
			where: {
				page: 1,
				limit: 5,
				status: 0
			},
			list: [],
			loaded: false,
			loading: false,
			orderInfo: {},
			status: ""
		};
	},
	watch: {
		"$yroute.query.types": function(newVal) {
			let that = this;
			if (newVal != undefined) {
				that.where.status = newVal;
				that.init();
			}
		},
		types: function() {
			this.getIndex();
		}
	},
	mounted: function() {
		let that = this;
		that.where.status = that.$yroute.query.types;
		that.current = "";
		that.getIndex();
	},
	onReachBottom() {
		!that.loading && that.getIndex();
	},
	methods: {
		goGoodsDeliver(item) {
			this.$yrouter.push({
				path: "/pages/orderAdmin/GoodsDeliver/index",
				query: {
					id: item.orderId
				}
			});
		},
		more: function(index) {
			if (this.current === index) this.current = "";
			else this.current = index;
		},
		modify: function(item, status) {
			this.change = true;
			this.orderInfo = item;
			this.status = status;
		},
		changeclose: function(msg) {
			this.change = msg;
		},
		async savePrice(opt) {
			let that = this,
				data = {},
				price = opt.price.toString(),
				refund_price = opt.refund_price.toString(),
				refund_status = that.orderInfo.refundStatus,
				remark = opt.remark;
			data.orderId = that.orderInfo.orderId;
			if (that.status == 0 && refund_status === 0) {
				try {
					await this.$validator({
						price: [
							required(required.message("金额")),
							num(num.message("金额"))
						]
					}).validate({
						price
					});
				} catch (e) {
					return validatorDefaultCatch(e);
				}
				data.price = price;
				setAdminOrderPrice(data).then(
					function() {
						that.change = false;
						uni.showToast({
							title: "改价成功",
							icon: "success",
							duration: 2000
						});
						that.init();
					},
					function() {
						that.change = false;
						uni.showToast({
							title: "改价失败",
							icon: "none",
							duration: 2000
						});
					}
				);
			} else if (that.status == 0 && refund_status === 1) {
				try {
					await this.$validator({
						refund_price: [
							required(required.message("金额")),
							num(num.message("金额"))
						]
					}).validate({
						refund_price
					});
				} catch (e) {
					return validatorDefaultCatch(e);
				}
				data.price = refund_price;
				data.type = opt.type;
				setOrderRefund(data).then(
					res => {
						that.change = false;
						uni.showToast({
							title: res.msg,
							icon: "none",
							duration: 2000
						});
						that.init();
					},
					err => {
						that.change = false;
						uni.showToast({
							title: res.msg,
							icon: "none",
							duration: 2000
						});
					}
				);
			} else {
				try {
					await this.$validator({
						remark: [required(required.message("备注"))]
					}).validate({
						remark
					});
				} catch (e) {
					return validatorDefaultCatch(e);
				}
				data.remark = remark;
				setAdminOrderRemark(data).then(
					res => {
						that.change = false;
						uni.showToast({
							title: res.msg,
							icon: "none",
							duration: 2000
						});
						that.init();
					},
					err => {
						that.change = false;
						uni.showToast({
							title: res.msg,
							icon: "none",
							duration: 2000
						});
					}
				);
			}
		},
		init: function() {
			this.list = [];
			this.where.page = 1;
			this.loaded = false;
			this.loading = false;
			this.getIndex();
			this.current = "";
		},
		getIndex: function() {
			let that = this;
			if (that.loading || that.loaded) return;
			that.loading = true;
			getAdminOrderList(that.where).then(
				res => {
					that.loading = false;
					that.loaded = res.data.length < that.where.limit;
					that.list.push.apply(that.list, res.data);
					that.where.page = that.where.page + 1;
				},
				err => {
					uni.showToast({
						title: res.msg,
						icon: "none",
						duration: 2000
					});
				}
			);
		},
		changeStatus: function(val) {
			if (this.where.status != val) {
				this.where.status = val;
				this.init();
			}
		},
		toDetail: function(item) {
			this.$yrouter.push({
				path: "/pages/orderAdmin/AdminOrder/index",
				query: {
					oid: item.orderId
				}
			});
		},
		offlinePay: function(item) {
			setOfflinePay({
				order_id: item.order_id
			}).then(
				res => {
					uni.showToast({
						title: res.msg,
						icon: "none",
						duration: 2000
					});
					this.init();
				},
				error => {
					uni.showToast({
						title: error.msg,
						icon: 'none',
						duration: 2000
					});
				}
			);
		}
	}
};
